<style type="stylesheet/scss" lang="scss">
  .page-dzjk {
    height: 100%;
    .bottom_bg{
      height: 1.1rem;
      background: url("../../assets/dzjkbanner.png") no-repeat;
      background-size: 100% 100%;
    }
    .tpage-dzjk-inner{
      position: absolute;
      top: .365rem;
      left: .17rem;
      right: .17rem;
      bottom: .8rem;
      padding: 0 0.21rem;
      background: #fff;
      border-radius: .1rem;
      display: flex;
      flex-direction: column;
      .total{
        flex: 9;
        font-size: .14rem;
        color: #333;
        border-bottom: 1px dashed #F3F3F3;
        span{
          font-size: .16rem;
        }
      }
      .chart{
        flex: 68;
        width: 100%;
        border-bottom:  1px dashed #F3F3F3;
      }
      .progress{
        flex: 23;
        width: 100%;
      }
    }
  }
</style>
<template>
    <div class="page-dzjk">
     <div class="page-dzjk-bg">
       <div class="bottom_bg"></div>
     </div>
    <div class="tpage-dzjk-inner" ref="inner">
      <div class="total am-flexbox">
        <div class="am-flexbox-item">
          <label></label>
          <span>226,271.862</span>
          <label>()</label>
        </div>
        <i class="iconfont icon-rili" @click="openDate"></i>
      </div>
      <div class="chart" ref="chart">
      </div>
      <div class="progress am-flexbox" ref="progress"></div>
    </div>
<!--      <mt-datetime-picker
        ref="picker"
        type="date"
        v-model="pickerValue"
        @confirm="choiceDate">
      </mt-datetime-picker>-->
    </div>
</template>
<script>
import { DatetimePicker } from 'mint-ui';
import Vue from 'vue';
// import { formatDate } from '../commons/utils';
// import echarts from 'echarts'
Vue.component(DatetimePicker.name, DatetimePicker);
var echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/pie');
require('echarts/lib/chart/bar');
// 
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
require('echarts/lib/component/legendScroll');

// const ERR_OK = 0;
export default {
  name: 'czy-dzjk',
  data () {
    return {
      pickerValue: '',
      pieChart: '',
      pieOptions: {
        title: {
          text: '2018-12-31',
          left: 'center',
          top: '26%',
          padding: [24, 0],
          textStyle: {
            color: '#333',
            fontSize: 10,
            align: 'center'
          }
        },
        tooltip: {
          trigger: 'item',
          textStyle: {
            fontSize: 14
          }
        },
        legend: {
          bottom: '10%'
        },
        series: [{
          type: 'pie',
          center: ['50%', '35%'],
          labelLine: {
            normal: {
              smooth: 0.1,
              length: 15,
              length2: 8
            }
          },
          label: {
            normal: {
              fontSize: 10,
              formatter: '{c}'
            }
          },
          hoverOffset: 5,
          radius: ['30%', '50%'],
          color: ['#FE7725', '#FFC000', '#317FFF'],
          data: [{value: 88212.765, name: ''}, {value: 78212.765, name: ''}, {value: 68212.765, name: ''}]
        }]
      },
    };
  },
  mounted () {
    this.$nextTick(() => {
      this.drawPie('.chart');
      this.drawBar('.progress')
    })
  },
  methods: {
    openDate () {
      this.$refs.picker.open();
    },
    drawPie (className) {
      this.pieChart = echarts.init(document.querySelector(className));
      this.pieChart.setOption(this.pieOptions);
      // this.getPieData('/api/dijk', new Date());
    },
    drawBar (className) {
      let chart = echarts.init(document.querySelector(className));
      let baifenbi = [0.98], zongjindu = [1], year = ['2018'];
      let option = {
        color: ['#317FFF'],
        grid: {
          top: 'middle',
          left: 0,
          right: 0
        },
        xAxis: [{
          show: false,
        },
        {
          show: false,
        }
        ],
        yAxis: {
          type: 'category',
          axisLabel: {
            show: false, // Y
          },
          itemStyle: {

          },
          axisTick: {
            show: false, // Y
          },
          axisLine: {
            show: false, // Y
          },
          data: year,
        },
        series: [
          {
            show: true,
            type: 'bar',
            barGap: '-100%',
            barWidth: '20', // 
            itemStyle: {
              normal: {
                barBorderRadius: 15,
                color: '#CAD1DD'
              },
            },
            z: 1,
            data: zongjindu,
          },
          // --------------------------------------------------//
          {
            show: true,
            type: 'bar',
            barGap: '-100%',
            barWidth: '20', // 
            itemStyle: {
              normal: {
                barBorderRadius: 20, // 
              },
            },
            max: 1,
            label: {
              normal: {
                show: true,
                // 
                formatter: function (data) {
                  return (baifenbi[data.dataIndex] * 100).toFixed(1) + '%';
                },
              }
            },
            labelLine: {
              show: false,
            },
            z: 2,
            data: baifenbi,
          },
          {
            show: true,
            type: 'bar',
            xAxisIndex: 1, // X!!!!!!!!!!!!!!!!!!!!!!!!
            barGap: '-100%',
            barWidth: '20', // 
            itemStyle: {
              normal: {
                barBorderRadius: 20,
                color: 'rgba(49, 127, 255,0.05)'
              },
            },
            label: {
              normal: {
                show: true,
                position: [0, '-120%'],
                rich: { // 
                  gray: {
                    color: '#333',
                  },
                },
                formatter: function (data) {
                  return '{gray|' + '' + '}' + '{gray|' + (baifenbi[data.dataIndex] * 100 + '%') + '}';
                },
              }
            },
            data: zongjindu
          }

        ]
      };
      chart.setOption(option);
    }/*,
    choiceDate (date) {
      this.getPieData('/api/dijk', date);
    },
    getPieData (url, dt) {
      dt = formatDate(dt, 'yyyy-MM-dd hh:mm').split(' ')[0];
      this.$http.get(url, { params: {date: dt} }).then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          let data;
          if (response.data) {
            data = [
              {value: response.data.gs, name: ''},
              {value: response.data.ds, name: ''},
              {value: response.data.cs, name: ''}];
          } else {
            data = [{value: 0, name: ''},
              {value: 0, name: ''},
              {value: 0, name: ''}]
          }
          this.pieOptions.title.text = dt;
          this.pieOptions.series[0].data = data;
          this.pieChart.setOption(this.pieOptions);
        }
      }).catch(msg => {
        console.log(msg);
      });
    } */
  }
};
</script>



